input[type='checkbox'] {
  $size: 16px;
  -o-appearance: none;
  -moz-appearance: none;
  -webkit-appearance: none;
  appearance: none;
  width:$size;
  height:$size;
  background-image:url(../images/form/checkbox.png);
  background-size:auto $size;
  &:focus { outline:none; background-position:-$size 0; }
  &:checked { background-position:-($size * 2) 0; }
  &::-ms-check { opacity:0; }
}

select.form-control { 
  &::-ms-expand {display:none;}
  &:optional,
  &:required {-webkit-appearance:none; -moz-appearance:none; appearance:none; padding-right:25px; background-repeat:no-repeat; background-position:right 8px center;
    background-image: url(data:image/png;base64,);

  }
  // &:focus {border-color: #aaa;}
}


.upload-set { position:relative;display:table; table-layout:fixed; width:100%; 
  .form-control[readonly] { background-color:#fff; }
  &+& { margin-top:5px; }
}
.upload-set-inner { position:relative; padding-right:32px; cursor:pointer; }
.upload-set-input-group { width:100%; }
.upload-set-file { position:absolute; z-index:10; top:0; right:0; width:100%; height:100%; font-size:0; opacity:0; -ms-filter:"alpha(opacity=0)"; cursor:pointer; }
.upload-set-del { position:absolute; bottom:0; right:0; width:31px; }
.upload-set-display { display:block; border:1px solid #ccc; border-bottom:none; padding:5px; }
.upload-set-thumb { max-width:100%; }
.upload-multiple-add { margin-top:5px; }

.js-toggle-img,
.modal-body .js-toggle-img { width:40%; }
.js-toggle-img.is-lg { width:auto; max-width:100%; }

.input-file-wrap { }
.input-file-wrap { }
.input-file-box { overflow:hidden; 
  .btn-duplicator { margin-top:5px; float:right; }
  .btn-remove { margin-top:5px; float:left; }
}
